<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input class="inp" maxlength="11" placeholder="请输入手机号码" type="text" v-model="mobile">
        </div>
        <div class="form-item">
          <input class="inp" maxlength="5" placeholder="请输入图形验证码" type="text" v-model="picCode">
          <img @click="getCode" :src="picUrl" alt="">
        </div>
        <div class="form-item">
          <input class="inp" placeholder="请输入短信验证码" type="text" v-model="smsCode">
          <button @click="this.getSmCode">
            {{ seconde === totalSecond ? '获取验证码' : seconde + `秒后重新发送`}}
          </button>
        </div>
      </div>

      <div class="login-btn" @click="Login">登录</div>
    </div>
  </div>
</template>

<script>

import { getCode, getSCode, codeLogin } from '@/api/login/'

export default {
  name: 'LoginPage',
  data () {
    return {
      picCode: '', // 用户输入的图形验证码
      picKey: '', // 将来请求传递的图形验证码
      picUrl: '', // 存储请求渲染的图片地址
      totalSecond: 60, // 总秒数
      seconde: 60, // 倒计时的秒数
      timer: null, // 定时器
      mobile: '15751776629', // 手机号
      smsCode: '246810' // 短信验证码

    }
  },
  async created () {
    this.getCode() // 加上this 调用的自身的
  },
  methods: {
    // 刷新验证码图形
    async getCode () {
      const { data: { base64, key } } = await getCode()
      this.picUrl = base64
      this.picKey = key
      this.$toast('图形刷新成功')
    },

    // 获取短信
    async getSmCode () {
      // ！！一定要记得调用
      if (!this.validFn()) {
        return
      }
      if (!this.timer && this.seconde === this.totalSecond) {
        const rere = await getSCode(this.picCode, this.picKey, this.mobile)
        console.log(rere)
        this.$toast('发送成功，请注意查收')
        this.timer = setInterval(() => {
          this.seconde--
          if (this.seconde <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.seconde = this.totalSecond
          }
        }, 1000)
      }
    },

    // 登入
    async Login () {
      if (!this.validyzm()) {
        return
      }
      const res = await codeLogin(this.mobile, this.smsCode)
      console.log(res)
      this.$store.commit('user/setUserInfo', res.data)
      this.$toast('登入成功')
      // this.$router.push('/')
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    },

    // 校验手机号和验证码
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的验证码')
        return false
      }
      return true
    },

    // 登入校验的手机号和短信验证码
    validyzm () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确手机号')
        return false
      }
      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast('请输入正确的验证码')
        return false
      }
      return true
    }
  },

  destroyed () {
    clearInterval(this.timer)
  }

}

</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
